import React from 'react';
import { Image } from 'semantic-ui-react'

const covidDescription = () => {

    return (
        <div className='ui grid container'>
            <div className="centered row">
                <h1>Ecuador COVID-19 dashboard</h1>
            </div>
            <div className='row'>
                <h2>Description</h2>
            </div>
            <div className='row'>
            Dashboard that presents COVID-19 data. It uses Python's library Dash for implementation.
            </div>
            <div className='centered row'>
                <Image src={require("../../images/ecuacovid.PNG")} size='medium'/>
            </div>
            <div className='spaced row'>
                <a href="http://ecuacovid.herokuapp.com/">
                    <div className='ui small button'>
                        <i className='external alternate icon'></i> Check it out
                    </div>
                </a>
                <a href="https://github.com/jomavera/dashboardData">
                    <div className='ui small button'>
                        <i className='github icon'></i> Repo
                    </div>
                </a>
            </div>
        </div>
    );
}


export default covidDescription;